.modal-container {
  background-color: #17262c;
  color: #fff;
  width: 65%;
  height: 80%;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 8rem auto;
  padding: 1rem 0;
  border: none;
  font-family: sans-serif;
  z-index: 999;
}

.modal-container h1 {
  font-size: 30px;
  text-transform: uppercase;
  margin-top: 2rem;
}

.modal-container h3 {
  color: rgb(230, 230, 230);
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 2rem;
}

.project-modal-info {
  width: 65%;
