header{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
}

.main-nav{
    width: 100%;
    height: 4rem;
    padding: 0 2.5rem;
    background: #26383f;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
}

.logo a{
    font-family: 'Lato', sans-serif;
    font-size: 1.4rem;
    color: #feffff;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    padding: .75rem 0rem;
}

.logo a:hover{
    color: #feffff;
}

header h1{
    font-size: 1.3rem;
    text-transform: uppercase;
    color: #feffff;
    position: absolute;
    font-weight: 200;
    right: 0;
    top: 0;
    padding-top: 1.1rem;
    padding-right: 5.5rem;
}

.hamburger-menu{
    height: 2.2rem;
    width: 2.2rem;
    background-color: #eee;
    border-radius: .3rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
}

.hamburger-menu:hover{
    box-shadow: inset .5rem .5rem 1rem #cacaca, inset -.5rem -.5rem 1rem #fff;
}

.bar{
    width: 1rem;
    height: 2px;
    background-color: #333;
    display: flex;
    position: absolute;
    transition: background-color .5s;
}

.bar::before,
.bar::after{
    content: '';
    position: absolute;
    width: inherit;
    height: inherit;
    background-color: #333;
    transition: all .5s;
}

.bar::before{
    top: -.6rem;
}

.bar::after{
    top: .6rem;
}

.open .bar{
    background-color: transparent;
}

.open .bar::before{
    top: 0;
    transform: rotate(135deg);
}

.open .bar::after{
    top: 0;
    transform: rotate(-135deg);
}

.nav-list{
    width: 35rem;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #17252a;
    transform: translateX(55rem);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    box-shadow: -.5rem 0 1rem rgba(0, 0, 0, .2);
    z-index: 1500;
    transition: transform .5s;
}

.nav-item{
    list-style: none;
    border-bottom: 2px solid #2692c0;
}

.nav-item a{
    color: #feffff;
    font-size: 1.2rem;
    text-transform: uppercase;
}

.nav-item a:hover{
    color: #feffff;
}

.nav-list .nav-item:hover{
    border-bottom: 2px solid #17252a;
}

.open .nav-list{
    transform: translateX(0);
}

/* nav bar ends */

